@import "sass/index";

.wrapper {
  padding-top: calc(#{$spacing} * 10);

  @media (max-width: $mobileScreen) {
    padding-top: calc(#{$spacing} * 4);
  }
}
.flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: $littleScreen) {
    justify-content: space-around;
  }
}
.row {
  flex-direction: row;
}
.secondaryPlace {
  background: use(--template-statisticsBlock-tab-placeBackground);
  border-radius: 17px;
  width: 120px;
  height: 116px;
  @media (max-width: $mobileScreen) {
    width: 96px;
    height: 94px;
  }

  @media (max-width: $littleScreen) {
    padding: 15px 0;
  }
}
.topPlace {
  background: use(--template-statisticsBlock-tab-placeBackground);
  border-radius: 17px;
  width: 157px;
  height: 152px;
  margin: 0 calc(#{$spacing} * 5.25);

  @media (max-width: $mobileScreen) {
    margin: 0 calc(#{$spacing} * 2);
    width: 109px;
    height: 106px;
  }

  @media (max-width: $littleScreen) {
    margin: 0;
    padding: 15px 0;
  }
}

.circle {
  display: flex;
  background: use(--template-statisticsBlock-tab-avatarBackground);
  border-radius: 50%;
}
.topPlaces {
  padding-bottom: calc(#{$spacing} * 2);
}
.topUserLogo {
  width: 48px;
  height: 48px;
}
.userLogo {
  width: 32px;
  height: 32px;
}

.tableAvatar {
  margin: 0 15px;
}
.userName {
  margin-top: calc(#{$spacing} * 2);

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 14px;
  color: use(--template-statisticsBlock-tab-color);
  letter-spacing: 0;
  line-height: 16px;

  @media (max-width: $mobileScreen) {
    font-size: 12px;
  }
}

.relative {
  position: relative;
}
.medal {
  position: absolute;
  bottom: -19px;

  @media (max-width: $mobileScreen) {
    bottom: -23px;
  }
}
.tableWrapper {
  padding-top: calc(#{$spacing} * 4);
}

.tableCellWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.userNameCell {
  justify-content: flex-start;
}

.userNameUnit {
  max-width: 100px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @media (max-width: 350px) {
    max-width: 60px;
  }
}
